

export const artworkAPI = {}

artworkAPI.fetchAllArtwork = async () => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await fetch(`/api/artwork/`)
            resolve(await response.json())
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}
artworkAPI.fetchArtwork = async () => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await fetch(`/api/artwork/:tokenId`)
            resolve(await response.json())
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}

artworkAPI.createArtwork = async (accessToken, data) => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await fetch(`/api/artwork/create`, {
                headers: {
                    'Authorization': `Bearer ${accessToken}`,
                    'Content-Type': 'application/json',
                },
                method: 'POST',
                body: JSON.stringify(data),
            })
            resolve(await response.json())
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}
