artworkAPI.getArtworkTransaction = async ({tokenId}) => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await fetch(`/api/artwork/get/transactions/`+tokenId, {
                headers: {
                    'Content-Type': 'application/json',
                },
                method: 'GET',
            })
            resolve(await response.json())
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}