import React from 'react'
import { Container, Grid, Typography } from '@material-ui/core'
import { useSelector, useDispatch } from 'react-redux'
import { makeStyles } from '@material-ui/core/styles'

import { selectLiveArtworks } from '../../store/artwork/artworkSlice'

import ArtworkCard from '../../components/Card/ArtworkCard'

import './artworks.css'

const Artworks = () => {
  const artworks = useSelector((state) => selectLiveArtworks(state))

  return (
    <Container maxWidth="lg">
      <Typography
        style={{
          paddingTop: '96px',
          paddingBottom: '24px',
          borderBottom: '2px solid',
          marginBottom: '24px',
        }}
        variant="h5"
        component="h6"
      >
        <span>-</span> Live auctions{' '}
      </Typography>

      <div className="artworks-grid">
        {artworks &&
          artworks.map((artwork) => {
            return (
              <div className="item" key={artwork._id}>
                <ArtworkCard artwork={artwork} />
              </div>
            )
          })}

        {/* <Grid direction="row"
                    justify="flex-start"
                    alignItems="center"
                    justify="center" container className={classes.root} spacing={2}>
                    <Grid item xs={12}>
                        <Grid container spacing={3}>
                            {artworks && artworks.map((artwork) => {
                                return (
                                    <Grid key={artwork._id} item md={3}>
                                        <ArtworkCard artwork={artwork} />
                                    </Grid>
                                )
                            })}


                        </Grid>
                    </Grid>
                </Grid> */}
      </div>
    </Container>
  )
}
export default Artworks
