import React, { useEffect, useState } from 'react'
import { Link, useHistory } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { Container, Grid, makeStyles, Button } from '@material-ui/core'

import Loading from '../../components/Loading'
import ArtworkCard from '../../components/Card/ArtworkCard'
import AvatarImg from '../../images/avatar.jpg'
import CoverImg from '../../images/cover.jpg'
import {
  selectCreatorById,
  fetchAllCreators,
} from '../../store/creator/creatorSlice'

import {
  selectMyCollection,
  selectMyCreation,
} from '../../store/artwork/artworkSlice'

import Clip from '../../components/Clip'
import { isVideo } from '../../utils'
import { creatorAPI } from '../../api/creator'

const IMG_BASE = 'https://ipfs.io/ipfs/'

const useStyles = makeStyles(() => ({
  root: {
    height: '300px',
    position: 'relative',
  },
  coverContrainer: {
    height: '400px',
    position: 'absolute',
    top: -150,
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
  },
  avatarContainer: {
    position: 'absolute',
    bottom: '0',
    left: '75px',
  },
  avatarImg: {
    height: '150px',
    width: '150px',
    borderRadius: '100px',
  },
  btn_o_md: {
    textAlign: 'center',
    textDecoration: 'none',
    willChange: 'transform',
    fontWeight: '600',
    padding: '8px 16px',
    transition: 'all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s',
    cursor: 'pointer',
    backgroundColor: 'transparent',
    color: '#00000',
    borderWidth: '2px',
    borderStyle: 'solid',
    borderColor: '#E6E6E6',
    fontSize: '14px',
    borderRadius: '999px',
    '&:hover': {
      backgroundColor: '#000',
      color: '#fff',
    },
  },
}))

const Creator = (props) => {
  const classes = useStyles()
  const dispatch = useDispatch()
  const history = useHistory()

  const { creator: creatorUsername } = props.match.params

  const selectedCreator = useSelector((state) =>
    selectCreatorById(state, creatorUsername),
  )
  const [creator, setCreator] = useState(selectedCreator)

  const myCreation = useSelector((state) =>
    selectMyCreation(state, creatorUsername),
  )
  const myCollection = useSelector((state) =>
    selectMyCollection(state, creatorUsername),
  )

  const userSlice = useSelector((state) => state.user)
  const user = userSlice.user
  const creatorStatus = useSelector((state) => state.creator.status)

  useEffect(() => {
    ;(async () => {

      if (!creator && creatorStatus === 'succeeded') {
        let fetchedCreator = await creatorAPI.fetchUser({username:creatorUsername})
        fetchedCreator ? setCreator(fetchedCreator) : history.push('/')
      }
    })()
  }, [creator, dispatch, creatorStatus])

  if (!creator) {
    // if (creatorStatus !== 'loading') {
    //   dispatch(fetchAllCreators())
    // }

    return <Loading />
  }


  let { coverImg, avatar, publicAddress, username, fullname, email } = creator
  coverImg = coverImg ? coverImg : CoverImg
  avatar = avatar ? avatar : AvatarImg
  fullname = fullname ? fullname : ''
  publicAddress = ' '
  return (
    <div>
      <Grid container className={classes.root}>
        <Grid className={classes.coverContrainer} container>
          {isVideo(coverImg) ? (
            <Clip url={IMG_BASE + coverImg} />
          ) : (
            <img
              style={{ height: '100%', width: '100%' }}
              src={IMG_BASE + coverImg}
            />
          )}
        </Grid>
        <div className={classes.avatarContainer}>
          {isVideo(avatar) ? (
            <Clip url={IMG_BASE + avatar} />
          ) : (
            <img className={classes.avatarImg} src={IMG_BASE + avatar} />
          )}
        </div>
      </Grid>

      <Grid
        style={{
          paddingTop: 50,
        }}
        container
      >
        <Grid container item xs={12} md={3}>
          <Container>
            <div
              style={{
                fontWeight: '400',
                letterSpacing: '0.6px',
                paddingLeft: '12px',
                transition: 'all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s',
                cursor: 'pointer',
                '&:hover': {
                  boxShadow: '0px 8px 15px rgb(0 0 0 / 25%)',
                  transform: 'translateY(-2px)',
                },
              }}
            >
              {publicAddress.slice(0, 4)}...
              {publicAddress.slice(
                publicAddress.length - 4,
                publicAddress.length,
              )}
            </div>

            <h1
              style={{
                margin: '0px 0px 5px',
                fontSize: '46px',
                fontWeight: '600',
                letterSpacing: '-0.05em',
                fontSize: '36px',
                lineHeight: '1.1',
              }}
            >
              {fullname}
            </h1>
            <h2
              style={{
                fontSize: '36px',
                lineHeight: '1.2',
                margin: '0px',
                background:
                  'linear-gradient(110.78deg, rgb(118, 230, 80) -1.13%, rgb(249, 214, 73) 15.22%, rgb(240, 142, 53) 32.09%, rgb(236, 81, 87) 48.96%, rgb(255, 24, 189) 67.94%, rgb(26, 75, 255) 85.34%, rgb(98, 216, 249) 99.57%)',
                WebkitBackgroundClip: 'text',
                WebkitTextFillColor: 'transparent',
              }}
            >
              @{username.toLowerCase()}
            </h2>

            <div
              style={{
                display: 'flex',
                alignItems: 'center',
                padding: '30px 0',
              }}
            >
              <div>
                <div
                  style={{
                    fontSize: '24px',
                    fontWeight: '600',
                  }}
                >
                  0
                </div>
                <div
                  style={{
                    fontSize: '18px',
                    fontWeight: '600',
                    color: '#7F7F7F',
                  }}
                >
                  Followers
                </div>
              </div>

              <div>
                {user && user.publicAddress == publicAddress ? (
                  <Link style={{ textDecoration: 'none' }} to="/profile/edit">
                    <Button className={classes.btn_o_md}>Edit Profile</Button>
                  </Link>
                ) : (
                  <Button className={classes.btn_o_md}>Follow </Button>
                )}
              </div>
            </div>
          </Container>
        </Grid>
        <Grid container item xs={12} md={9}>
          <Container>
            <div style={{ textAlign: ' center' }}>
              <div>
                <h1>creation</h1>
                <Grid
                  direction="row"
                  justify="flex-start"
                  alignItems="center"
                  justify="center"
                  container
                  spacing={2}
                >
                  <Grid item xs={12}>
                    <Grid container spacing={3}>
                      {myCreation &&
                        myCreation.map((artwork) => {
                          return (
                            <Grid key={artwork._id} item md={4}>
                              <ArtworkCard artwork={artwork} />
                            </Grid>
                          )
                        })}
                      {!myCreation || myCreation.length <= 0 ? (
                        <div>
                          <h1>Your creation is empty.</h1>
                          <p>
                            Start building your creation by placing bids on
                            artwork.
                          </p>
                        </div>
                      ) : (
                        ''
                      )}
                    </Grid>
                  </Grid>
                </Grid>
              </div>

              <br />
              <div>
                <h1>Collection</h1>
                <Grid
                  direction="row"
                  justify="flex-start"
                  alignItems="center"
                  justify="center"
                  container
                  spacing={2}
                >
                  <Grid item xs={12}>
                    <Grid container spacing={3}>
                      {myCollection &&
                        myCollection.map((artwork) => {
                          return (
                            <Grid key={artwork._id} item md={3}>
                              <ArtworkCard artwork={artwork} />
                            </Grid>
                          )
                        })}
                      {!myCollection || myCollection.length <= 0 ? (
                        <div>
                          <h1>Your Collection is empty.</h1>
                          <p>
                            Start building your collection by placing bids on
                            artwork.
                          </p>
                        </div>
                      ) : (
                        ''
                      )}
                    </Grid>
                  </Grid>
                </Grid>
              </div>
            </div>
          </Container>
        </Grid>
      </Grid>
    </div>
  )
}
export default Creator
