import { useSelector } from 'react-redux'
import {Link} from 'react-router-dom'
import moment from 'moment'
import { selectCreatorById } from '../../store/creator/creatorSlice'

import {isVideo} from '../../utils/file'

import Clip from '../../components/Clip'

import { ReactComponent as ExitIcon } from '../../images/svg/exit.svg'

import { IMG_BASE } from '../../constant'

import './index.css'

const Transction = ({ transaction }) => {

    const user = transaction.username? transaction.username : transaction.from
    const creator = useSelector((state) => selectCreatorById(state, user))

    let description = ""
    if(transaction.type == "Mint"){

    }

    return (
      <div className="tranasction">
        <Link to={"/"+user} className="avatar-image">
      { isVideo(creator.avatar)? (<div className="media">
          <Clip url={IMG_BASE + creator.avatar} className="video" />
        </div>): (<div className="media">
          <img className="img" src={IMG_BASE + creator.avatar} title={user} />
        </div>) 
        }
        </Link>
        <div className="description">
          <div>
            Artwork {transaction.type}
            {' '}by <span className="creator-username">@{transaction.username ? transaction.username : transaction.from}</span>
          </div>
          <div>{moment(transaction.date).format('LLL')}</div>
        </div>
        <div className="price">
          {transaction.type == 'Bid' ? (
            <div>
              <div>{(parseInt(transaction.value) / 1e18).toFixed(4)} ETH</div>
              {/* <div>${(parseInt(transaction.value) / 1e18).toFixed(4)}</div> */}
            </div>
          ) : (
           <div></div>
          )}
          <div className="action">
          
          <ExitIcon />
        
      </div>
        </div>
        
      </div>
    )
  }

  export default Transction