import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import Web3 from 'web3'
import {creatorAPI} from '../../api/creator'
const initialState = {
    creators: [],
    error: null,
    status: 'idle'
}


export const fetchAllCreators = createAsyncThunk('creator/fetchAllCreators', async () => {
    //fetch user from here
    let creators = await creatorAPI.fetchAllCreators()
    return creators

})
export const fetchCreator = createAsyncThunk('creator/fetchCreator', async (username) => {
    //fetch user from here
    let creator = await creatorAPI.fetchCreator(username)
    return creator

})


const creatorSlice = createSlice({
    name: 'creator',
    initialState,
    reducers: {

    },
    extraReducers: {
        [fetchAllCreators.pending]: (state, action) => {
            state.status = 'loading'
        },
        [fetchAllCreators.fulfilled]: (state, action) => {
            state.status = 'succeeded'
            state.creators = action.payload
        },
        [fetchAllCreators.rejected]: (state, action) => {
            state.status = 'failed'
            state.error = action.error.message
        },
        [fetchCreator.pending]: (state, action) => {
            state.status = 'loading'
        },
        [fetchCreator.fulfilled]: (state, action) => {
            state.status = 'succeeded'
            state.creators = [...state.creators, action.payload]
        },
        [fetchCreator.rejected]: (state, action) => {
            state.status = 'failed'
            state.error = action.error.message
        },
    }
})

export default creatorSlice.reducer

export const selectCreatorById = (state, creatorId) =>{
    let isAddress = Web3.utils.isAddress(creatorId)
    if(isAddress){
        return state.creator.creators.find(creator => creator.publicAddress === creatorId)
    }else{
        return state.creator.creators.find(creator => creator.username === creatorId)

    }
}
