import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  selectArtworkById,
  syncAuction,
} from '../../store/artwork/artworkSlice'
import Countdown from '../../components/Countdown'

import { getAuction } from '../../services/Web3/token'

import AuctionFooter  from './auction/AuctionFooter'

const ETH_RATE = 2082.87

const ArtworkDetails = ({ artwork }) => {
  const dispatch = useDispatch()

  const artworkSlice = useSelector((state) => state.artwork)

  const [artworkAuction, setArtworkAuction] = useState({
    active: false,
    creator: '0',
    endTime: '0',
    owner: '0',
    value: '0',
  })

  useEffect(() => {
    console.log('artwork use effect ')
    if (artwork) {
      verifyAuction(artwork)
    }
  }, [artwork])

  const verifyAuction = async (artwork) => {
    try {
      let { active, creator, value, endTime, owner } = await getAuction(
        artwork.tokenId,
      )
      if (active) {
        if (active != artwork.isAuction ||  value !== artwork.price || endTime !== artwork.endTime) {
          if (artworkSlice.status !== 'loading') {
            dispatch(syncAuction({ tokenId: artwork.tokenId }))
          }
        }
      }

      setArtworkAuction({
        active: active,
        creator: creator,
        value: value,
        endTime: endTime,
        owner: owner,
      })
    } catch (err) {
        setArtworkAuction({
            active: artwork.active,
            creator: artwork.creator,
            value: artwork.value,
            endTime: artwork.endTime,
            owner: artwork.owner,
          })
      console.log(err)
    }
  }

  

  let price = artwork.price ? artwork.price : 0

  if (artworkAuction && artworkAuction.active) {
    price = artworkAuction.value
  }
  price = parseFloat(price) / 1e18

  const RightDetail = (artworkAuction) => {
    if (artworkAuction && artworkAuction.active) {
      return (
        <div>
          <div className="mini-text">Auction ending in</div>
          <Countdown type="main-countdown" endTime={artworkAuction.endTime} />
        </div>
      )
    } else {
      return(
        <div>
        <div>Owned By</div>
        <span className="owner">{artworkAuction.owner.slice(0, 6)}...</span>
      </div>
      )
    }
  }



  return (
    <div className="auction-details-container">
      <div className="auction-details">
        <div className="left-detail">
          <div className="mini-text"> Current Bid</div>
          <div className="big-text">{price} ETH </div>
          <div className="faded-text">$ {price * ETH_RATE}</div>
        </div>

        <div className="">{RightDetail(artworkAuction)}</div>
      </div>

      <AuctionFooter artworkAuction={artworkAuction} artwork={artwork} />
    </div>
  )
}

