

export const creatorAPI ={}

creatorAPI.fetchAllCreators = async () => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await fetch(`/api/users/creators`)
            resolve(await response.json())
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}



creatorAPI.fetchCreator =  async (data) => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await fetch(`/api/users/get/creator/`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(data),
            })
            resolve(await response.json())
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}

